import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import {
  Col,
  Collapse,
  // DatePicker,
  Divider,
  Empty,
  Input,
  InputNumber,
  Row,
  Select,
  Spin
  // Tooltip
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/accPayAgingReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box, customCollapseStyle } from "../../../App/app.style";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;
// const Search = Input.Search;
// const { RangePicker } = DatePicker;
const InputGroup = Input.Group;

const redirectTo = "/dashboard/report-menus";

class AccPayAgingReport extends Component {
  state = {
    filter: {
      id_location: null,
      id_supplier: null,
      ap_aging: "from_transaction_date",
      fromDays: 1,
      toDays: 20
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getAccPayAgingReports } = this.props;
    getAccPayAgingReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.accPayAgingReport.error) {
      notification("error", this.props.accPayAgingReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.accPayAgingReport.success) {
      notification("success", this.props.accPayAgingReport.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getAccPayAgingReports } = this.props;
    let filter = this.filterValue();

    getAccPayAgingReports(1, value, filter, this.state.filter);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getAccPayAgingReports } = this.props;
    this.setState({
      filter: {
        id_location: null,
        id_supplier: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss")
      },
      search: null
    });
    getAccPayAgingReports();
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else if (name === "ap_aging" && value !== this.state.filter.ap_aging) {
      this.setState({
        filter: {
          ...this.state.filter,
          ap_aging: value
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
    if (name === "id_location") {
      this.setState({
        filter: {
          ...this.state.filter,
          id_location: value,
          id_supplier: null
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.id_supplier) {
      string
        ? (string += "&id_supplier=" + filter.id_supplier)
        : (string += "id_supplier=" + filter.id_supplier);
    }
    string
      ? (string +=
          "&" +
          filter.ap_aging +
          "=1&fromDays=" +
          filter.fromDays +
          "&toDays=" +
          filter.toDays)
      : (string +=
          filter.ap_aging +
          "=1&fromDays=" +
          filter.fromDays +
          "&toDays=" +
          filter.toDays);

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getAccPayAgingReports } = this.props;
    //const { current_page } = this.props.accPayAgingReport;
    let filter = this.filterValue();

    getAccPayAgingReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getAccPayAgingReports } = this.props;
    this.setState({
      filter: {
        id_location: null,
        id_supplier: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss")
      },
      search: null
    });
    getAccPayAgingReports();
  };

  render() {
    const columns = [
      {
        dataIndex: "purchase_invoice_no",
        rowKey: "purchase_invoice_no",
        title: getLanguage({ id: "invoiceNo" }),
        width: "14%",
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              children: (
                <strong style={{ marginLeft: "50%" }}>{row.key}</strong>
              ),
              props: {
                colSpan: 8
              }
            };
          }

          return text;
        }
      },
      {
        dataIndex: "supplier_name",
        rowKey: "supplier_name",
        title: getLanguage({ id: "supplierName" }),
        width: "10%",
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return text;
        }
      },
      {
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        date: true,
        title: getLanguage({ id: "transactionDate" }),
        width: "11%",
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return moment(text).format("DD MMMM YYYY");
        }
      },
      {
        dataIndex: "due_date",
        rowKey: "due_date",
        title: getLanguage({ id: "dueDate" }),
        date: true,
        width: "11%",
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return moment(text).format("DD MMMM YYYY");
        }
      },
      {
        dataIndex: "aging",
        rowKey: "aging",
        title: getLanguage({ id: "aging" }),
        width: "8%",
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return text;
        }
      },
      {
        dataIndex: "currency",
        rowKey: "currency",
        title: getLanguage({ id: "sidebar.currency" }),
        width: "9%",
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return text;
        }
      },
      {
        align: "right",
        dataIndex: "total_amount",
        parse: true,
        rowKey: "total_amount",
        title: getLanguage({ id: "totalAmount" }),
        width: "14%",
        render: (text, row) => {
          if (row.key === "Total") {
            return (
              <strong>
                <NumberFormat value={parseFloat(text)} />
              </strong>
            );
          }

          return <NumberFormat value={parseFloat(text)} />;
        }
      },
      {
        align: "right",
        dataIndex: "paid_amount",
        parse: true,
        rowKey: "paid_amount",
        title: getLanguage({ id: "paidAmount" }),
        width: "14%",
        render: (text, row) => {
          if (row.key === "Total") {
            return (
              <strong>
                <NumberFormat value={parseFloat(text)} />
              </strong>
            );
          }

          return <NumberFormat value={parseFloat(text)} />;
        }
      },
      {
        align: "right",
        dataIndex: "total_owing",
        parse: true,
        rowKey: "total_owing",
        title: getLanguage({ id: "totalOwing" }),
        width: "14%",
        render: (text, row) => {
          if (row.key === "Total") {
            return (
              <strong>
                <NumberFormat value={parseFloat(text)} />
              </strong>
            );
          }

          return <NumberFormat value={parseFloat(text)} />;
        }
      }
    ];

    // Data setup
    const {
      acc_pay_aging_reports,
      locations,
      suppliers,
      filter,
      filter_select
    } = this.props.accPayAgingReport;
    //console.log(this.props.accPayAgingReport.base_currency); //console.log(this.state);
    const dataSource = [];

    // const { user } = this.props.auth;
    // let { permissions } = user ? user : [];
    // permissions = permissions ? permissions : [];

    // let total_amounts = 0;
    // let total_paid_amounts = 0;
    acc_pay_aging_reports.map((data, key) => {
      // total_amounts += parseFloat(data.total_amount);
      // total_paid_amounts += parseFloat(data.paid_amount);

      dataSource.push({
        ...data,
        key
      });
    });

    // dataSource.push({
    //   key: "Total",
    //   total_amount: total_amounts,
    //   paid_amount: total_paid_amounts
    // });

    const { loading } = this.props.accPayAgingReport;

    const fileName = getLanguage({ id:"accPayAgingReports.pageHeader" });
    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="accPayAgingReports.pageHeader" />}
            marginBottom={true}
          >
            <div>
              <PrintWrapper
                componentRef={ref => (this.componentRef = ref)}
                title={<IntlMessages id="accPayAgingReports.pageHeader" />}
                columns={columns}
                agingFilter={
                  filter_select &&
                  filter_select.fromDays &&
                  filter_select.toDays
                    ? filter_select.fromDays +
                      " - " +
                      filter_select.toDays +
                      " " +
                      getLanguage({ id: "days" }) +
                      " " +
                      (filter_select.ap_aging === "from_transaction_date"
                        ? getLanguage({
                            id: "accPayAgingReports.filter.transDate"
                          })
                        : getLanguage({
                            id: "accPayAgingReports.filter.dueDate"
                          }))
                    : null
                }
                data={dataSource}
                filter={filter_select}
              />

              <ReactToPrint
                trigger={() => <ButtonPrint loading={loading} />}
                content={() => this.componentRef}
                onBeforePrint={() => (document.title = getFileName(fileName, null, false))}
                onAfterPrint={() => (document.title = siteConfig.documentTitle)}
                pageStyle={
                  "@page { size: landscape; }" +
                  "body {" +
                  "-webkit-print-color-adjust: exact;" +
                  "} "
                }
              />

              <PDFWrapper
                columns={columns}
                data={dataSource}
                fileName={fileName}
                filter={filter_select}
                landscape={true}
                loading={loading}
                locations={locations}
                scale={0.55}
                title="accPayAgingReports.pageHeader"
              />

              <ExcelWrapper
                title="accPayAgingReports.pageHeader"
                fileName={fileName}
                loading={loading}
                columns={columns}
                data={dataSource}
                filter={filter_select}
                locations={locations}
              />

              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>

          {/* <div className="isoInvoiceTableBtn">
            <Search
              placeholder="Location Code, Location Name"
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.searchFilter" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div> */}

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={24}>
                        <label>
                          <IntlMessages id="sidebar.location" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "id_location"
                          )}
                          value={
                            this.state.filter.id_location
                              ? this.state.filter.id_location
                              : undefined
                          }
                          style={{ width: "95%" }}
                          placeholder={<IntlMessages id="select.location" />}
                          optionFilterProp="children"
                          filterOption={true}
                          allowClear={true}
                          //showArrow={false}
                        >
                          {locations && locations.length > 0
                            ? locations.map((data, key) => (
                                <Option key={key} value={data.id_location}>
                                  {data.location_code +
                                    " (" +
                                    data.location_name +
                                    ")"}
                                </Option>
                              ))
                            : null}
                        </Select>
                      </Col>
                    </Row>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="sidebar.supplier" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_supplier"
                      )}
                      value={
                        this.state.filter.id_supplier
                          ? this.state.filter.id_supplier
                          : undefined
                      }
                      style={{ width: "100%" }}
                      placeholder={
                        this.state.filter.id_location ? (
                          <IntlMessages id="select.supplier" />
                        ) : (
                          <IntlMessages id="noLocation" />
                        )
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      allowClear={true}
                      notFoundContent={
                        loading ? (
                          <Spin size="small" />
                        ) : this.state.filter.id_location ? (
                          <Empty description={<IntlMessages id="noData" />} />
                        ) : (
                          <Empty
                            description={
                              <IntlMessages id="purchaseOrders.item.noLocation" />
                            }
                          />
                        )
                      }
                      //showArrow={false}
                    >
                      {suppliers && suppliers.length > 0
                        ? suppliers
                            .filter(
                              data =>
                                data.id_location ===
                                this.state.filter.id_location
                            )
                            .map((data, key) => (
                              <Option key={key} value={data.id_supplier}>
                                {data.supplier_code +
                                  " (" +
                                  data.supplier_name +
                                  ")"}
                              </Option>
                            ))
                        : null}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <Row>
                      <Col span={11}>
                        <label>
                          <IntlMessages id="sidebar.APAgingReport" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "ap_aging"
                          )}
                          value={
                            this.state.filter.ap_aging
                              ? this.state.filter.ap_aging
                              : "from_transaction_date"
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option value="from_transaction_date">
                            <span style={{ textTransform: "capitalize" }}>
                              {getLanguage({
                                id: "accPayAgingReports.filter.transDate"
                              })}
                            </span>
                          </Option>
                          <Option value="from_due_date">
                            <span style={{ textTransform: "capitalize" }}>
                              {getLanguage({
                                id: "accPayAgingReports.filter.dueDate"
                              })}
                            </span>
                          </Option>
                        </Select>
                      </Col>
                      <Col span={13}>
                        <label>
                          <IntlMessages id="accPayAgingReports.filter.length" />
                        </label>
                        <br />
                        <InputGroup compact>
                          <InputNumber
                            min={0}
                            placeholder="Min"
                            style={{ width: 82, textAlign: "center" }}
                            onChange={this.handleFilterChange.bind(
                              this,
                              "fromDays"
                            )}
                            value={
                              this.state.filter.fromDays
                                ? this.state.filter.fromDays
                                : 0
                            }
                          />
                          <Input
                            style={{
                              width: 30,
                              borderLeft: 0,
                              pointerEvents: "none",
                              backgroundColor: "#fff"
                            }}
                            placeholder="~"
                            disabled
                          />
                          <InputNumber
                            style={{
                              width: 82,
                              textAlign: "center",
                              borderLeft: 0
                            }}
                            placeholder="Max"
                            min={this.state.filter.fromDays}
                            onChange={this.handleFilterChange.bind(
                              this,
                              "toDays"
                            )}
                            value={
                              this.state.filter.toDays
                                ? this.state.filter.toDays
                                : 1
                            }
                          />
                          <Input
                            style={{
                              width: 60,
                              borderLeft: 0,
                              pointerEvents: "none",
                              backgroundColor: "#fff"
                            }}
                            placeholder={getLanguage({ id: "days" })}
                            disabled
                          />
                        </InputGroup>
                      </Col>
                    </Row>
                  </Col>
                  <Col
                    span={12}
                    style={{
                      display: "flex",
                      flexDirection: "column",
                      justifyContent: "flex-end"
                    }}
                  >
                    <Row justify="start" gutter={1} type="flex">
                      <Col
                        span={8}
                        style={{
                          marginTop: "auto"
                        }}
                      >
                        <Button
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                        >
                          <IntlMessages id="filter.only" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clickFilter" />} />
            ) : acc_pay_aging_reports.length === 0 &&
              !this.props.accPayAgingReport.loading ? (
              <Empty
                description={
                  <IntlMessages id="accPayAgingReports.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="details" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    accPayAgingReport: state.accPayAgingReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(AccPayAgingReport);
